<template>
  <v-container>
    <h1>„Éó„É¨„Ç§ÁîªÈù¢</h1>
    <!-- ÂçòÂÖÉÈÅ∏Êäû -->
    <v-select v-model="selectedLesson" :items="lessons" item-title="name" item-value="id" label="ÂçòÂÖÉ„ÇíÈÅ∏Êäû" class="mb-4"
      @update:model-value="onLessonSelect"></v-select>

    <v-card v-if="currentSentence">
      <v-card-title>ÊñáÁ´†„ÇíÊ≠£„Åó„Åè‰∏¶„ÅπÊõø„Åà„Å¶„Åè„Å†„Åï„ÅÑ</v-card-title>
      <v-card-text>
        <v-chip-group v-model="selectedWords" column multiple>
          <v-chip v-for="word in shuffledWords" :key="word" :value="word">
            {{ word }}
          </v-chip>
        </v-chip-group>
      </v-card-text>

      <v-card>„ÅÇ„Å™„Åü„ÅÆÂõûÁ≠î</v-card>
      <v-card-text>
        <v-chip-group column multiple>
          <v-chip v-for="word in selectedWords" :key="word" :value="word">
            {{ word }}
          </v-chip>
        </v-chip-group>
      </v-card-text>

      <v-card-actions v-if="currentSentence.length > 0">
        <v-btn @click="popString" color="primary">
          1„Å§Êàª„Çã
        </v-btn>
      </v-card-actions>

      <v-card-actions v-if="checkLength()">
        <v-btn @click="checkAnswer" color="primary">
          ÂõûÁ≠î„ÇíÁ¢∫Ë™ç
        </v-btn>
      </v-card-actions>
    </v-card>

    <v-alert v-if="feedback" :type="feedbackType" class="mt-4">
      {{ feedback }}
    </v-alert>

    <div v-if="currentSentence">

      <div v-if="isCorrect() && feedbackType === 'success'">
        <div v-if="isEnd()">
          <v-btn @click="startScreen">
            <p>ÁµÇ‰∫Ü</p>
          </v-btn>
        </div>

        <div v-else>
          <v-btn @click="nextSentence" color="secondary" class="mt-4">
            <p>Ê¨°„ÅÆÂïèÈ°å</p>
          </v-btn>
        </div>
      </div>

    </div>
  </v-container>
</template>

<script>
import { db } from '../firebase/init'
import { collection, getDocs } from 'firebase/firestore'


export default {
  name: 'PlayScreen',
  data() {
    return {
      lessons: [],
      selectedLesson: null,
      sentences: [],
      currentSentence: null,
      shuffledWords: [],
      selectedWords: [],
      feedback: '',
      feedbackType: 'info'
    }
  },
  async mounted() {
    await this.loadLessons()
    this.loadSentences()
  },
  methods: {
    async loadLessons() {
      try {
        const lessonsRef = collection(db, 'lessons')
        const querySnapshot = await getDocs(lessonsRef)
        this.lessons = querySnapshot.docs.map(doc => ({
          id: doc.id,
          ...doc.data()
        }))
      } catch (error) {
        console.error('LessonsË™≠„ÅøËæº„Åø„Ç®„É©„Éº:', error)
      }
    },
    onLessonSelect() {
      const lesson = this.lessons.find(l => l.id === this.selectedLesson)
      if (lesson) {
        this.sentences = [...lesson.sentences]
        this.nextSentence()
      }
    },
    loadSentences() {
      // Firestore„Åã„ÇâË™≠„ÅøËæº„Çì„Å†„Éá„Éº„Çø„Çí‰ΩøÁî®
      if (this.lessons.length > 0) {
        // ‰æãÔºöÊúÄÊñ∞„ÅÆÂçòÂÖÉ„Çí‰ΩøÁî®
        const latestLesson = this.lessons[this.lessons.length - 1]
        this.sentences = [...latestLesson.sentences]
        this.nextSentence()
      }
    },
    nextSentence() {
      if (this.sentences.length > 0) {
        this.currentSentence = this.sentences.pop()
        this.shuffledWords = this.shuffleArray(this.currentSentence.split(','))
        this.selectedWords = []
        this.feedback = ''
        this.feedbackType = 'info'
      } else {
        this.currentSentence = null
        this.feedback = '„Åô„Åπ„Å¶„ÅÆÂïèÈ°å„ÅåÁµÇ‰∫Ü„Åó„Åæ„Åó„Åü„ÄÇ'
        this.feedbackType = 'success'
      }
    },
    shuffleArray(array) {
      for (let i = array.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [array[i], array[j]] = [array[j], array[i]]
      }
      return array
    },
    isCorrect() {
      const correctAnswer = this.currentSentence.split(',').join(' ')
      const userAnswer = this.selectedWords.join(' ')
      return correctAnswer === userAnswer
    },
    checkAnswer() {
      if (this.isCorrect()) {
        this.feedback = 'Ê≠£Ëß£„Åß„ÅôÔºÅ'
        this.feedbackType = 'success'
      } else {
        this.feedback = `üò£ÊÆãÂøµÔºéÔºéÔºé„ÇÇ„ÅÜ‰∏ÄÂ∫¶„ÇÑ„Å£„Å¶„Åø„Çà„ÅÜÔºÅüò£`
        this.feedbackType = 'error'
      }
    },
    checkLength() {
      const correctAnswer = this.currentSentence.split(',').join(' ')
      const userAnswer = this.selectedWords.join(' ')
      return (userAnswer.length == correctAnswer.length)
    },
    popString() {
      this.selectedWords.pop()
    },
    isEnd() {
      return this.sentences.length === 0
    },
    startScreen() {
      this.$router.push('/')
    }
  }
}
</script>
